import React from 'react';
import ToggleImpl from './DSToggleImpl';
import { togglePropTypes } from './props';

const DSToggle = ({
  containerProps = {},
  hasError = false,
  readOnly = false,
  disabled = false,
  checked = undefined,
  labelOn = 'ON',
  labelOff = 'OFF',
  name = '',
  value = '',
  size = 's',
  ...otherProps
}) => (
  <ToggleImpl
    {...otherProps}
    checked={checked}
    containerProps={containerProps}
    disabled={disabled}
    hasError={hasError}
    labelOff={labelOff}
    labelOn={labelOn}
    name={name}
    readOnly={readOnly}
    size={size}
    value={value}
  />
);

DSToggle.propTypes = togglePropTypes;

export default DSToggle;
